<script>
import HelloWorld from "../../components/HelloWorld.vue";
import swip from "../../components/b.vue";
export default {
  data: () => ({
    productList: [],
    carouselPictures: []
  }),
  computed: {
    userInfo() {
      return JSON.parse(this.$store.state.memberInfo);
    }
  },
  created() {
    if (this.userInfo) {
      console.log("success");
      this.getWxsdk();
    }
  },
  mounted() {
    this.$axios.notLoggedIn(`ym/mall-product/list?page=1&limit=5`).then(msg => {
      let List = msg.records;
      List.forEach(res => {
        this.productList.push(res);
      });
    });
    this.$axios.notLoggedIn(`ym/banner/list`).then(msg => {
      msg.forEach(res => {
        this.carouselPictures.push(res);
      });
      //console.log(msg);
    });
    // this.getUserInfo()
  },
  components: {
    HelloWorld,
    swip
  },
  methods: {
    getWxsdk() {
      console.log("start");
    },
    productSearch(e) {
      this.$axios
        .notLoggedIn(
          `ym/mall-product/list?page=1&limit=5&keyword=${e.target.value}`
        )
        .then(msg => {
          this.productList = [];
          let List = msg.records;
          List.forEach(res => {
            this.productList.push(res);
          });
        });
      //console.log(e.target.value);
    },
    getUserInfo() {
      this.$axios.get(`ym/memberuser/user-info`).then(msg => {
        window.console.log(msg.data);
        if (msg.data.code === 0) {
          this.$store.commit("SAVEMEMBERINFO", JSON.stringify(msg.data.data));
        } else {
          //console.log('获取用户信息失败！')
        }
      });
      this.$axios.get(`ym/menu/list`).then(msg => {
        window.console.log(msg);
      });
    },
    jump(id) {
      this.$router.push({
        path: "/commodityDetails",
        query: {
          id: id
        }
      });
    }
  }
};
</script>
